@media screen and (max-width: 900px) {
  .column {
    width: 50%;
  }
}
@media screen and (max-width: 768px) {
  .home{
    height: fit-content;
  }
  .full{
    grid-template-rows: repeat(auto-fit, minmax(80px, auto));
    grid-template-columns: 50% 50%;
    grid-gap:0;
    font-family: 'Josefin Slab', serif;
  }
  .welcomeP, .welcome, .contactNav{
    display:none;
  }
  .logo, .logo2{
    display:block;
    height: fit-content;
    font-size: 4em;
    text-align: center;
    width: 100%;
    z-index:2;
  }
  .logo{
    grid-row: 1;
    grid-column: 1;
    color: gold;
  }
  .logo2{
    grid-row:1;
    grid-column:2;
    color:  turquoise
  }
  .work,
  .aboutme,
  .mail{
    grid-area: auto;
  }
  .aboutme{
    grid-row: 2;
  
  }
  .work{
    grid-row:3;
  }
  .mail{
    grid-row:4;
  }
  .iconsnav{
    padding:0;
    height: 100%;
    grid-row: 1/7;
    grid-column: 2;
    flex-wrap: wrap;
    place-content: center;
    background-image:url("../images/rose-egg.svg");
    background-position:center center;
  }
  .iconsnav .icon{
    background-color: turquoise;
    border-radius: 50%;
    display:flex;
    justify-content: center;
    align-items: center;
    width: 80px;
    height: 80px; 
    margin: 10px;
  }
  .text{
    display: none;
  }
  .iconsnav i,a{
    color: white;
  }
  .iconsnav i{
    font-size: 3em;
  }
  .aboutbox-article{
    display: block;
    margin:0;
  }
  .aboutbox p{
    padding: 10px;
    margin: 0;
  }
  .date{
    display: none;
  }
  .projectbox{
    height: auto;
  }
  .projectbox:hover img{
    opacity:inherit;
  }
  .projectbox::before, .projectbox::after{
    display: none;
  }
  .projectbox:not(:first-of-type){
    margin-top:5px;
  }
  .projectbox ul{
    position: static;
    opacity:1;
    transform: none;
    }
    .projectbox li:last-child {
      width: auto;
      padding:0;
  
    }
    .button i{
      font-size: 3em;
    }
    .navTop{
      background-color: rgba(0, 0, 0, 0.4);
      z-index:1;
      margin:0;
      padding: 5px;
      position: fixed; /* Make it stick/fixed */
      top: 0; /* Stay on top */
      width: 100%; /* Full width */
      transition: top 0.6s; /* Transition effect when sliding down (and up) */
    }
    .navTop .icon{
      display: block;
      color:white;
      font-weight: 100;
    }
    
    /* Responsive layout - makes the two columns stack on top of each other instead of next to each other */

      .column {
        width: 100%;
      }

