.home{
  overflow: hidden;
  width: 100%;
  font-family: 'Fredericka the Great', cursive;
}
.full {
  height: 100vh;
  display: grid;
  grid-template-rows: 2em repeat(4,1fr) 2em;
  grid-template-columns: repeat(3,1fr) 25vw 40vw;
  grid-gap: 15px;
}
.full > a:not(.welcome) {
 place-self: center;
  font-size:2.3em;
  color:brown;
  z-index:2;
  padding:0 10%;
}
.full > a:hover{
  color: yellow;
  transition: color 2s;
}
.aboutme {
  grid-area: 2 / 2 / 2 / 4;
}
.work {
  grid-area: 3 / 1 / 3 / 4;
  
}
.mail{
  grid-area: 4/2/4/4;
}
.contactNav{
  grid-area:5/2/5/5;
  text-align: center;
}
.welcome {
  grid-area: 1 / 4 / 7 / 6;
  background-image: url(../images/rose-egg.svg);
  background-size: cover;
  background-position: right;
  background-repeat: no-repeat;
  z-index:1; 
}
.welcomeP{
  grid-area: 2 / 5 / 6 / 6;
  z-index:2;
  font-family: 'Josefin Slab', serif;
  color:navy;
  font-size:3.8em;
  text-align: right;
  border-right: 5px dotted navy;
  padding-right:20px;
  margin:20px;
  align-self:center;
  line-height: 1.1em;
}
.logo{
  display: none;
}
.iconsnav {
  font-family: 'Josefin Slab', serif;
  grid-area: 1 / 1 / 1 / 6;
  display:flex;
  align-items: center;
  text-align: center;
  background-color:white;
  height:fit-content;
  padding: 20px 0 0 20px;
  text-transform: lowercase;
}
.iconsnav a{
  margin: 0 10px 0 10px;
}
.iconsnav .icon{
  display: none;
}