.navTop {
    display: flex;
    height: fit-content;
    place-content: space-evenly;
    padding:30px;
    font-family: 'Josefin Slab', serif;
    font-weight: bolder;
    
  
  }
  .navbar--hidden {
    top: -50px;
  }
  .navTop .text{
    color: gold;
  }
  .navTop .text:hover{
    color: navy;
    transition: color 2s;
  }
  .navTop i{
  font-size:3em;
  }
  .navTop .icon{
    display: none;
  }
