@import url(https://fonts.googleapis.com/css?family=Josefin+Slab|Open+Sans:300|Cabin+Sketch|Fredericka+the+Great|Lobster|Quicksand&display=swap);
.home{
  overflow: hidden;
  width: 100%;
  font-family: 'Fredericka the Great', cursive;
}
.full {
  height: 100vh;
  display: grid;
  grid-template-rows: 2em repeat(4,1fr) 2em;
  grid-template-columns: repeat(3,1fr) 25vw 40vw;
  grid-gap: 15px;
}
.full > a:not(.welcome) {
 place-self: center;
  font-size:2.3em;
  color:brown;
  z-index:2;
  padding:0 10%;
}
.full > a:hover{
  color: yellow;
  -webkit-transition: color 2s;
  transition: color 2s;
}
.aboutme {
  grid-area: 2 / 2 / 2 / 4;
}
.work {
  grid-area: 3 / 1 / 3 / 4;
  
}
.mail{
  grid-area: 4/2/4/4;
}
.contactNav{
  grid-area:5/2/5/5;
  text-align: center;
}
.welcome {
  grid-area: 1 / 4 / 7 / 6;
  background-image: url(/portfolio/static/media/rose-egg.d5dc20f0.svg);
  background-size: cover;
  background-position: right;
  background-repeat: no-repeat;
  z-index:1; 
}
.welcomeP{
  grid-area: 2 / 5 / 6 / 6;
  z-index:2;
  font-family: 'Josefin Slab', serif;
  color:navy;
  font-size:3.8em;
  text-align: right;
  border-right: 5px dotted navy;
  padding-right:20px;
  margin:20px;
  align-self:center;
  line-height: 1.1em;
}
.logo{
  display: none;
}
.iconsnav {
  font-family: 'Josefin Slab', serif;
  grid-area: 1 / 1 / 1 / 6;
  display:flex;
  align-items: center;
  text-align: center;
  background-color:white;
  height:-webkit-fit-content;
  height:-moz-fit-content;
  height:fit-content;
  padding: 20px 0 0 20px;
  text-transform: lowercase;
}
.iconsnav a{
  margin: 0 10px 0 10px;
}
.iconsnav .icon{
  display: none;
}
#about{
  display: flow-root;
}
.aboutbox article:first-child {
  border-bottom: 1px solid white;
  text-align: center;
  padding-bottom: 50px; 
}
.aboutbox>article:not(.bio){
  padding-top: 50px;
}
.bio .date{
float:left;
}
.skills .date{
float:right;
}
.skills>p span{
  color:gold;
}
.date span {
  font-family: "Lobster", cursive;
  font-size: 3em;
  line-height: 90px;
  color: white;
}
.date {
  border: 4px gold double;
  shape-outside: circle(95px);
  width: 90px;
  height: 90px;
  border-radius: 90px;
  text-align:center;
  margin: 50px;
  /* background-color: gold; */
}


.row{
  margin: 8px -16px;
}
.row:after {
  content: "";
  display: table;
  clear: both;
}
.row,
.row > .column {
  padding: 8px;
}
.column{
  float: left;
  width: 50%;
}
.projectbox{
  position:relative;
  height: 500px;
  padding:10px;
  text-align:center;
}
.projectbox img {
  background-color:white;
  object-fit:cover;
  /* height:500px; */
  width:100%;
  -webkit-transition:opacity ease-out 250ms;
  transition:opacity ease-out 250ms;
  opacity:1;
}
.projectbox:hover img{
  opacity:0.1;
}
.projectbox::before{
  content:"";
  position:absolute;
  top:1.25em;
  bottom:1.25em;
  left:1.25em;
  right:1.25em;
  -webkit-transform:scale(0,1);
          transform:scale(0,1);
  -webkit-transition: -webkit-transform ease-out 250ms;
  transition: -webkit-transform ease-out 250ms;
  transition: transform ease-out 250ms;
  transition: transform ease-out 250ms, -webkit-transform ease-out 250ms;
  border-bottom:1px solid white;
}
.projectbox::after{
  content:"";
  position:absolute;
  top:1.25em;
  bottom:1.25em;
  left:1.25em;
  right:1.25em;
  -webkit-transform:scale(1,0);
          transform:scale(1,0);
  -webkit-transition: -webkit-transform ease-out 250ms;
  transition: -webkit-transform ease-out 250ms;
  transition: transform ease-out 250ms;
  transition: transform ease-out 250ms, -webkit-transform ease-out 250ms;
  border-left: 1px solid white;
}
.projectbox:hover::before{
  -webkit-transform: scale(1.05,1);
          transform: scale(1.05,1);
}
.projectbox:hover::after{
  -webkit-transform: scale(1,1.05);
          transform: scale(1,1.05);
  }
  .projectbox ul{
  position:absolute;
  top:50%;
  left:50%; 
  -webkit-transform: translate(-50%,-50%); 
          transform: translate(-50%,-50%);
  opacity:0;
  z-index: 1;
  -webkit-transition: opacity ease-out 250ms;
  transition: opacity ease-out 250ms;
}
.projectbox:hover ul{
  opacity:1;
}
h2 {
