* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 0;
  margin: 0;
  text-decoration: none;
  list-style: none;
  text-shadow: 1px 1px 1px rgba(0,0,0,0.004);
  text-rendering: optimizeLegibility !important;
  -webkit-font-smoothing: antialiased !important;
  font-size: calc(14px + (26 - 14) * ((100vw - 300px) / (1600 - 300)));
  box-sizing: border-box;
 
  /* text-transform: uppercase; */
}
*:before,
*:after {
  box-sizing: border-box;
}
html {
scroll-behavior: smooth;
background-color:silver;
height: fit-content;
overflow-y: scroll;
-webkit-overflow-scrolling:touch;
}
body{
  padding-bottom:20px;
background-image: url("./images/rose-blue.svg");
background-size:cover;
background-attachment: fixed;
background-repeat: repeat;
width: 100%;
}
/* border page */
#top, #bottom, #left, #right {
  background: whitesmoke;
  position: fixed;
  z-index:1;
}
#left, #right {
  top: 0; bottom: 0;
  width: 5px;
}
#left { left: 0; }
#right { right: 0; }
  
#top, #bottom {
  left: 0; right: 0;
  height: 5px;
}
#top { top: 0; }
#bottom { bottom: 0; }
/* border page end*/

section:not(.home) {
  max-width: 1000px;
  margin: auto;
  padding: 70px 50px 50px 50px;
  background-color: rgba(0, 0, 0, 0.4);
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.4);
  }
  /* .content{
    width: 100%;
    margin-top:70px;
  } */
  h1 {
    font-size: 3em;
    text-align:center;
    font-weight:100;
    font-family: 'Fredericka the Great', cursive;
    word-break: break-all;
    padding-bottom:50px;
}
/* h1::before, h1::after{
  content:" ";
  display: block;
  border-bottom: 5px double navy; 
  width:2em;
  margin:auto;
  padding: 5px;
} */
h1, h2, h3, article,p{
  color:white; 
}
.text{
  color: gold;

}
/* animation */

.trademark{
  font-family: 'Cabin Sketch', cursive;
  z-index:1;
}
.logo, .logo2{
  display:none;
}

.puff-in-center {
  -webkit-animation: puff-in-center 0.5s cubic-bezier(0.680, -0.550, 0.265, 1.550) both;
  animation: puff-in-center 0.5s cubic-bezier(0.680, -0.550, 0.265, 1.550) both;
}
/* keyframes */

@-webkit-keyframes puff-in-center {
  0% {
    -webkit-transform: scale(2);
    transform: scale(2);
    -webkit-filter: blur(4px);
    filter: blur(4px);
    opacity: 0;
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
    -webkit-filter: blur(0px);
    filter: blur(0px);
    opacity: 1;
  }
}
@keyframes puff-in-center {
  0% {
    -webkit-transform: scale(2);
    transform: scale(2);
    -webkit-filter: blur(4px);
            filter: blur(4px);
    opacity: 0;
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
    -webkit-filter: blur(0px);
    filter: blur(0px);
    opacity: 1;
  }
}


@media screen and (max-width: 768px) {
  body{
    background-size:contain;
    padding:0;
  }
  #top, #bottom, #left, #right{
    display: none;
  }
  section:not(.home){
  margin:0 auto;
  padding:5em 0 0 0;
  background-color:transparent;
  box-shadow: none;
}
h2, article,p{
  color: navy;
}
.trademark{
  display: none;
}
  }